@import 'sass-rem';

.headerBottom {
  position: relative;
  //overflow-x: auto;
  //overflow-y: visible;
  z-index: 3;
  padding-right: rem(18px);
  &:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: rem(535px);
    background: linear-gradient(
                    90deg,
                    #262626 0%,
                    #1a1a1a 56.77%,
                    #000000 100%
    );
    @media (max-width: 767.98px) {
      width: 100%;
    }
  }
  @media(max-width: 767.98px) {
    padding: rem(6px) 0;
  }
}

.content {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 2;
}


.list {
  display: flex;
  gap: rem(5px);
  align-items: center;
  @media (max-width: 1024px) {
    display: none;
  }
}

.link {
  display: inline-block;
  position: relative;
  font-size: rem(14px);
  line-height: rem(16px);
  color: #696969;
  padding: rem(10px);
  white-space: nowrap;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: rem(-5px);
    right: rem(-5px);
    background: rgba(255, 255, 255, 0);
    transition: 0.3s;
    border-radius: rem(20px);
    z-index: -1;
  }

  &:hover,
  &.linkActive {
    color: #fff;

    &:before {
      background: #1f1f1f;
    }
  }

  &.linkCustom {
    text-transform: uppercase;
    color: #b7181b;
    padding: rem(5px 13px);
    border: 1px solid #b7181b;
    border-radius: rem(30px);
    font-style: italic;
    font-weight: 700;

    &:hover {
      color: #b7181b;
    }

    &:before {
      display: none;
    }
  }
}

.info {
  display: flex;
  align-items: center;
  gap: rem(15px);
  //padding: rem(15px);
  //margin-left: auto;
  @media (max-width: 767.98px) {
    margin-left: auto;
  }
}

.time {
  font-size: rem(14px);
  line-height: rem(16px);
  white-space: nowrap;
  color: #696969;
}

.apps {
  display: flex;
  align-items: center;
  gap: rem(15px);
}

.linkApp {
  display: inline-block;
  border-radius: 50%;
  font-size: 0;
  width: rem(40px);
  height: rem(40px);
  position: relative;

  &:before {
    content: '';
    position: absolute;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50% 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &.linkAppIphone {
    background: #fff;

    &:before {
      width: rem(18px);
      height: rem(21px);
      background-image: url('../../assets/images/svg/header-app-iphone.svg');
    }
  }

  &.linkAppAndroid {
    background: #7ea152;

    &:before {
      width: rem(22px);
      height: rem(16px);
      background-image: url('../../assets/images/svg/header-app-android.svg');
    }
  }
}

