@import 'sass-rem';

.menu {
	display: flex;
	//flex-direction: column;
	//position: fixed;
	position: relative;
	//top: rem(146px);
	//bottom: 0;
	//left: 0;
	padding: rem(8px 12px);
	background: #000;
	border-width: 0px 1px 1px 0px;
	border-style: solid;
	border-color: #232323;
	//width: rem(86px);
	//max-height: calc(100vh - #{rem(146px)});
	overflow-x: scroll;
	z-index: 5;

	@media (min-width: 768px) {
		display: none;
	}

	&::-webkit-scrollbar {
		height: rem(2px);
	}

	&::-webkit-scrollbar-track {
		background-color: #28293e;
		border-radius: rem(5px);
	}

	&::-webkit-scrollbar-thumb {
		border-radius: rem(5px);
		background: #8b8b97;
	}
}

.list {
	display: flex;
	//flex-direction: column;
	gap: rem(20px);
	//max-width: rem(55px);
}

.item {
	display: flex;
	justify-content: center;
	flex: 0 0 rem(48px);
}

.link {
	display: flex;
	justify-content: center;
	color: #8a8a8a;
	font-weight: 500;
	font-size: rem(12px);
	line-height: rem(14px);
	text-align: center;
	margin-left: rem(-5px);
	margin-right: rem(-5px);
	width: calc(100% + rem(10px));
	padding-top: rem(55px);
	position: relative;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		width: rem(52px);
		left: 50%;
		height: rem(52px);
		border-radius: rem(16px);
		background-color: #161616;
		background-repeat: no-repeat;
		background-size: contain;
		background-position: 50% 50%;
		opacity: 0.6;
		transition: 0.3s;
		border: 2px solid transparent;
		transform: translateX(-50%);
	}

	&:hover,
	&.active {
		color: #fff;

		&:before {
			background-color: #000;
			border-color: #f7ff13;
			border-radius: rem(13px);
			opacity: 1;
		}
	}
}

.game {
	&:before {
		background-size: rem(34px), rem(34px);
		opacity: 1;
	}
}

.top {
	&:before {
		background-image: url('../../assets/images/svg/menu-top.svg');
	}
}

