.live {
	&:after {
		content: '';
		width: rem(16px);
		height: rem(16px);
		position: absolute;
		top: rem(-4px);
		right: rem(-4px);
		background: url('../../assets/images/svg/menu-live.svg') no-repeat 50% 50% /
			contain;
	}
}

