@import "sass-rem";

.broadcast {
  &__reg {
    padding-top: rem(15px);
    display: flex;
    flex-direction: column;
    gap: rem(10px);
  }

  &__text {
    color: #9A9A9A;
    text-align: center;
    font-size: rem(14px);
    line-height: 142.857%;
    max-width: rem(225px);
    margin: 0 auto;
  }

  &__btn {
    max-width: rem(161px);
    margin: 0 auto;
    width: 100%;
  }
}

.lightBg {
  background: #1A1A1A;
}

.translation {
  background: #0D0D0D;
  padding: rem(15px);

  &.margin {
    margin-bottom: rem(20px);
  }

  &__more {
    padding: rem(11px);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #909090;
    width: 100%;
    border-radius: 2px;
    background: #1A1A1A;
    font-size: rem(12px);
    font-weight: 500;

    &:hover {
      color: #fff;
    }
  }
}

.header {
  display: flex;
  justify-content: space-between;
}

.headerTitle {
  font-weight: 500;
  font-size: rem(16px);
  line-height: rem(19px);
  color: #fff;
  text-align: center;
  text-transform: uppercase;
}

.headerBtn {
  display: inline-block;
  position: relative;
  font-size: 0;
  width: rem(16px);
  height: rem(20px);
  cursor: pointer;

  &.active {
    transform: rotateX(180deg);
  }

  &:hover {
    &:before {
      opacity: 1;
    }
  }

  &:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-repeat: no-repeat;
    background-size: contain;
    transition: .3s;
    opacity: .6;
  }

  &.headerBtnLock {
    &:before {
      width: rem(20px);
      height: rem(20px);
      background-image: url("../../assets/images/svg/broadcast-lock.svg");
    }
  }

  &.headerBtnArrow {
    &:before {
      width: rem(20px);
      height: rem(6px);
      background-image: url("../../assets/images/svg/broadcast-arrow.svg");
    }
  }
}

.list {
  margin-top: rem(24px);
  margin-bottom: rem(15px);
  text-align: center;
  font-weight: 400;
  font-size: rem(16px);
  line-height: rem(19px);
  color: #fff;
  display: flex;
  flex-direction: column;
  gap: rem(15px);
  align-items: center;
}

.item {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  position: relative;
  max-width: 100%;

  &.itemLive {
    padding-left: rem(22px);

    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      display: block;
      width: rem(16px);
      height: rem(16px);
      border-radius: rem(8px);
      background: #E71E30;
      border: rem(4px) solid #77222A;
      transform: translateY(-50%);
    }
  }
}

.btnMore {
  display: inline-block;
  width: 100%;
  background: #1A1A1A;
  border-radius: 2px;
  font-weight: 500;
  font-size: rem(12px);
  line-height: rem(14px);
  text-align: center;
  color: #909090;
  transition: .3s;
  text-transform: uppercase;
  padding: rem(11px);
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;

  &:hover {
    color: #fff;
  }
}

.liveImg {
  display: inline-block;
  margin-left: rem(-15px);
  margin-right: rem(-15px);
  min-width: calc(100% + rem(30px));
  height: rem(184px);
}