@import "sass-rem";

.limitSelf {
  display: flex;
  gap: rem(15px);
}

.hand {
  width: rem(87px);
  height: rem(87px);
  margin-top: rem(20px);
  margin-left: rem(40px);
  margin-right: rem(80px);
  background: url("../../assets/images/svg/return-hand.svg") no-repeat 50% 50% /contain;
  flex: 1 0 rem(70px);
  @media(max-width: 1199.98px) {
    margin: 0;
    height: rem(70px);
  }
}

.info {
  max-width: rem(594px);
}

.infoText {
  padding: rem(24px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  margin-bottom: rem(25px);
  font-weight: 400;
  font-size: rem(18px);
  line-height: 1.1666;
  color: #fff;
  @media(max-width: 1199.98px) {
    font-size: rem(16px);
    margin-bottom: rem(10px);
  }
}

.infoLabel {
  font-weight: 500;
  font-size: rem(18px);
  line-height: rem(21px);
  color: #fff;
  padding-left: rem(34px);
  cursor: pointer;
  margin-bottom: rem(40px);
  position: relative;
  display: inline-block;
  @media(max-width: 1199.98px) {
    margin-bottom: rem(24px);
  }
  @media(max-width: 767.98px) {
    font-size: rem(16px);
  }

  &:before {
    content: "";
    width: rem(24px);
    height: rem(24px);
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    background: url("../../assets/images/svg/return-checkbox.svg") no-repeat 50% 50% /contain;
  }

  &:after {
    content: "";
    width: rem(24px);
    height: rem(24px);
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    background: url("../../assets/images/svg/return-checkbox-active.svg") no-repeat 50% 50% /contain;
    opacity: 0;
  }

  &.infoLabelActive {
    &:after {
      opacity: 1;
    }
  }
}

.infoName {
  font-weight: 500;
  font-size: rem(18px);
  line-height: rem(21px);
  color: #F7FF13;
  margin-bottom: rem(10px);
}

.infoSelectWrapper {
  position: relative;
  margin-bottom: rem(24px);
  @media(max-width: 767.98px) {
  }
}

.mobileWrap {
  @media(max-width: 767.98px) {
    position: absolute;
    bottom: rem(27px);
    left: rem(20px);
    width: calc(100% - rem(40px));
  }
}

.infoSelect {
  padding: rem(15px 16px 14px);
  border: 1px solid #fff;
  cursor: pointer;
  font-weight: 400;
  font-size: rem(18px);
  line-height: rem(21px);
  color: rgba(255, 255, 255, 0.5);
  transition: .3s;
  position: relative;

  &:hover {
    color: #fff;
  }

  &:before {
    content: "";
    position: absolute;
    top: 50%;
    right: rem(15px);
    width: rem(12px);
    height: rem(7px);
    background: url("../../assets/images/svg/return-select-arrow.svg") no-repeat 50% 50% /contain;
    transform: translateY(-50%);
  }
}

.selectLimitList {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  border: 1px solid #fff;
  background: #1C1C1C;
  transform: translateY(-1px);
  height: rem(150px);
  overflow-y: auto;
  z-index: 2;

  &::-webkit-scrollbar {
    width: rem(5px);
  }

  &::-webkit-scrollbar-track {
    background-color: #28293e;
    border-radius: rem(5px);
  }

  &::-webkit-scrollbar-thumb {
    border-radius: rem(5px);
    background: #8b8b97;
  }

  &.selectLimitListOpen {
    display: block;
  }
}

.selectLimitItem {
  height: rem(50px);
  border-bottom: 1px solid #fff;
  background: #1C1C1C;
  padding: rem(15px 16px 14px);
  color: #fff;
  font-size: rem(18px);
  line-height: rem(21px);
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  position: relative;
  cursor: pointer;

  &:last-child {
    border-bottom: none;
  }
}

.btn {
  padding: rem(12px 27px);
  background: #FAFE13;
  border-radius: rem(3px);
  cursor: pointer;
  font-weight: 700;
  font-size: rem(17px);
  line-height: rem(20px);
  text-align: center;
  text-transform: uppercase;
  color: #000;
}
